import React, { useState, useEffect } from "react";
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import TopBar from './components/TopBar';
import FootBar from './components/FootBar'
import Main from './components/Main'
import Nosotros from './components/Nosotros'
import Topic from "./components/Topic";
import Publications from "./components/Publications";
import "./App.css";

function App() {
    const [posts, setPosts] = useState([])

  useEffect(() => {
    fetch('https://mundodeltrabajo.org/wp-json/wp/v2/posts?per_page=100&_fields=id,slug,title,categories,acf.autores,acf.fecha,yoast_head_json.og_image,content')
      .then(res => res.json())
      .then(posts => {  
        // Realizar la llamada a 'categories' endpoint para obtener nombres
        fetch('https://mundodeltrabajo.org/wp-json/wp/v2/categories?_fields=id,name')
          .then(res => res.json())
          .then(categoriesData => {
            // Crear un mapa de categorías para buscar fácilmente por id
            let categoriesMap = {};
            categoriesData.forEach(category => {
              categoriesMap[category.id] = category.name;
            });
  
            // Mapear los números de categorías en los nombres correspondientes
            let postsWithCategoryNames = posts.map(post => ({
              ...post,
              categories: post.categories.map(categoryId => categoriesMap[categoryId]),
            }));
  
            // Actualizar el estado con los posts modificados
            setPosts(postsWithCategoryNames);
          })
          .catch(err => console.log(err));
      })
      .catch(err => console.log(err));
  }, []);
  
  return (
    <BrowserRouter>
        <TopBar />
        <div className="content-center">
          <Routes>            
              <Route path="/" element={<Main  posts={posts} />} />
              <Route path="/noticias" element={<Publications defaultSection={'noticias'} />} />
              <Route path="/cursos" element={<Publications defaultSection={'cursos'} />} />
              <Route path="/articulos" element={<Publications defaultSection={'articulos'} />} />
              <Route path="/videos" element={<Publications defaultSection={'videos'} />} />
              <Route path="/nosotros/" element={<Nosotros />} />
              <Route path="/:slug/" element={<Topic  posts={posts} />} />
          </Routes>
        </div>    
        <FootBar />
    </BrowserRouter>
  );
}

export default App;
