import React from 'react'
import MainCoursesCard from './MainCoursesCard';

function MainCourses(props) {
  const renderTwo = props.courses.length >= 2;

  return (
    <>
      {renderTwo ? (
        <>
          <MainCoursesCard course={props.courses[0]} className="course-card-l" />
          <div className="splitter-line"></div>
          <MainCoursesCard course={props.courses[1]} className="course-card-r" />
        </>
      ) : (
        <MainCoursesCard course={props.courses[0]} />
      )}
    </>
  )
}

export default MainCourses