import React from 'react'
import ActiveSection from './ActiveSection'
import { Link, useLocation } from 'react-router-dom'

const Publications = ({defaultSection}) => {

  const location = useLocation();
  const [section, setSection] = React.useState(defaultSection ?? 'noticias')
  React.useEffect( () => {
    // Actualiza la sección basada en la ruta actual
    const path = location.pathname;
    if (path === '/noticias') {
      setSection('noticias');
    } else if (path === '/articulos') {
      setSection('articulos');
    } else if (path === '/cursos') {
      setSection('cursos');
    }
  }, [location.pathname])
  
  return (
    <>
      <nav>      
        <Link to={'/noticias'}><button onClick={() => setSection('noticias')} style={section === 'noticias' ? {backgroundColor: 'red'} : {}}>Noticias</button></Link>
        <Link to={'/articulos'}><button onClick={() => setSection('articulos')} style={section === 'articulos' ? {backgroundColor: 'red'} : {}}>Articulos</button></Link>
        <Link to={'/cursos'}><button onClick={() => setSection('cursos')} style={section === 'cursos' ? {backgroundColor: 'red'} : {}}>Cursos</button></Link>
        <Link to={'/videos'}><button onClick={() => setSection('videos')} style={section === 'videos' ? {backgroundColor: 'red'} : {}}>Videos</button></Link>
        {/* otros botones */}
      </nav>
      <section>
        <ActiveSection section={section} />
      </section>
    </>
  )
}

export default Publications