import React from 'react';
import News from './News';
import Courses from './Courses';
import Articles from './Articles';
import FeedVideos from './FeedVideos';

const ActiveSection = ({section}) => {
    switch (section) {
        case 'noticias':
          return <News />
        case 'articulos':
          return <Articles />
        case 'cursos':
          return <Courses />
        case 'videos':
          return <FeedVideos />
        default:
          return <News />
        }
}

export default ActiveSection